@import 'src/styles/vars';

.materiallibrary {
    width: 100%;
    display: flex;
    flex-direction: row;
}
.displayReferences{
    margin : 0 auto;
    padding: 30px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.paginate{
    display: flex;
    margin: 2% 0;
    justify-content: center;
    width: 100%;
}
@media all and (max-width: 1080px)
{
    .materiallibrary {
        flex-direction: column;
    }
}