.who{display:flex;flex-flow:row wrap;background-color:#ee4842;margin-bottom:30px;padding:30px}.who-title{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:center;align-items:center;border-bottom:3px solid #fffbec;padding-bottom:20px;width:60vw;margin:0 auto;margin-bottom:20px;color:#fffbec;font-size:1.5rem}.who-title-logo{width:50px;margin-right:30px;filter:drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.3))}.who p{display:flex;flex-flow:row nowrap;font-size:1.3rem;margin:0 auto;width:60vw;color:#fffbec;align-items:center}.everybody-conten{display:flex;justify-content:center}.everybody p{color:#333;margin:0;text-align:center;font-size:1.2rem;font-weight:600}.wordscloud{margin:2%;display:flex;justify-content:center}.simple-cloud{width:50%;line-height:3rem}.simple-cloud span{margin-right:20px}.content-actions{background-color:#fffbec;padding:30px;margin-bottom:30px}.actions{display:flex;flex-flow:column wrap;width:50vw;margin:0 auto}.actions-title{color:#333;border-bottom:3px solid #333;padding-bottom:20px}.actions-li{margin:20px 0 20px 0;display:flex;align-items:flex-end;justify-content:baseline}.actions-li p{color:#333;margin:0;text-align:left;font-size:1.2rem;font-weight:600}.actions-li-icons{filter:drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.3));margin-right:20px;width:60px}.about-philo{display:flex;flex-flow:row wrap;background-color:#00bdb6}.about-philo-title{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:center;align-items:center;border-bottom:3px solid #fffbec;padding-bottom:20px;width:60vw;margin:30px auto;margin-bottom:20px;color:#fffbec;font-size:1.5rem}.about-philo-icon{margin-bottom:-5px;color:#fffbec}.about-philo p{align-items:center;font-weight:600;margin:0 auto;padding-bottom:30px;width:70vw;color:#fffbec;font-size:1.2rem;text-align:center}.actions-passées{margin-bottom:30px}.actions-passées-title{border-bottom:3px solid #333;padding-bottom:20px;width:50vw;margin:30px auto;margin-bottom:20px;color:#333;font-size:1.5rem}.actions-passées p{font-weight:600;margin:0 auto;padding-bottom:30px;width:50vw;color:#333;font-size:1.2rem;text-align:center}.actions-passées-diapos{margin:0 auto;display:flex;flex-flow:row wrap;width:40%;justify-content:center}.actions-passées-body{margin:10px;width:250px;--clay-background: rgb(250, 150, 0);--clay-border-radius: 10px;padding:5px;text-decoration:none !important;cursor:pointer;transition:background-color .3s}.actions-passées-body:hover{background-color:#ffb23e}.actions-passées-body-link{text-decoration:none !important;display:flex;flex-direction:row;align-items:center}.actions-passées-body-link p{padding:0;text-transform:uppercase;color:#333;font-weight:600;font-size:1rem}.actions-passées-body-link-button{color:#fffbec}.actions-passées-body-pdf-modal{position:relative;top:10%;left:10%;width:75vw;height:75vh;border-radius:10px}@media all and (max-width: 810px){.who p{display:block}.actions-li p{text-align:center}.actions-li-icons{display:none}}