.testcouleur{color:#00bdb6;color:#fa9600;color:#f82216;color:#8e0616;color:#ffebcd}.bandeau-présentation{background-color:#00bdb6;display:flex;flex-wrap:wrap;flex-direction:row;align-items:center;justify-content:space-around;padding:1rem}.bandeau-présentation-texte{font-size:1.5rem;max-width:60%;color:#ffebcd;text-shadow:rgba(0,0,0,.3) 5px 3px 5px;text-align:center;width:60%}.bandeau-présentation-permanence{width:30%}.fonctionnement{padding:30px;display:flex;flex-wrap:wrap;flex-direction:row;justify-content:center}.fonctionnement-title{margin-top:0;font-size:2.5rem;color:#000;order:-1;flex:1 0 100%}.fonctionnement-content{max-width:200px;color:#000;font-weight:700;font-size:1rem;align-items:baseline}.fonctionnement-logo{width:100px;height:100px}.fonctionnement-logo2{margin-top:20px;display:flex;width:50px;height:50px}.categoriesDisplay{background-color:#fa9600;color:#ffebcd;text-shadow:rgba(0,0,0,.3) 5px 3px 5px;font-weight:700;font-size:1.5rem;display:flex;flex-wrap:wrap;flex-direction:row;align-items:center;justify-content:space-evenly}.categorie{margin:10px;--clay-background: #ffebcd;--clay-shadow-inset-primary: -8px -8px 16px 0 rgba(0, 0, 0, 0.15);padding:10px}.categorie-box{display:flex;flex-direction:column;align-items:center}.categorie-title{margin-top:30px;display:flex;flex:1 0 100%;justify-content:center}.categorie-img{width:100px;transition:transform .5s;filter:drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.5))}.categorie-img:hover{transform:scale(2);filter:drop-shadow(3px 5px 5px rgba(0, 0, 0, 0.5))}.actu{padding:30px;display:flex;flex-wrap:wrap;flex-direction:row;justify-content:space-evenly;background-color:#00bdb6}.actu-title{margin-top:0;font-size:2.5rem;color:#ffebcd;text-shadow:rgba(0,0,0,.3) 5px 3px 5px;order:-1;flex:1 0 100%}.actu-post{margin:20px;--clay-background: blanchedalmond;--clay-shadow-inset-primary: -8px -8px 16px 0 rgba(0, 0, 0, 0.15);color:#000;align-items:baseline}.actu-img{width:300px;padding:30px;filter:drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.5))}.contact-container{padding:30px;display:flex;flex-wrap:wrap;flex-direction:row;justify-content:space-evenly;align-items:center}.contact-container-iframe{height:250px;padding:30px;border-style:hidden;border-radius:50px;filter:drop-shadow(5px 5px 10px rgba(0, 0, 0, 0.3))}@media all and (max-width: 1080px){.bandeau-présentation{flex-direction:column}.bandeau-présentation-permanence{width:50%}}